body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    padding-top: 6rem;
    min-height: 100vh;
    background-image: url(/Bartosz94094/pokedex/static/media/background2.3825123d.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    padding-bottom: 3rem;
}

.navbar {
    background-color: rgb(253, 53, 67) !important;
}

.card-header {
    background-color: rgb(255, 204, 1) !important;
    color: black;
}

.details {
    border: 2px solid rgb(54, 94, 170) !important;
}

.database-loading {
    color: rgb(193, 193, 193);
    -webkit-animation: loading-txt-anim 2s linear infinite;
            animation: loading-txt-anim 2s linear infinite;
}

@-webkit-keyframes loading-txt-anim {
    50% {
        opacity: 0;
    }

    100% {
        opacity: 100%;
    }
}

@keyframes loading-txt-anim {
    50% {
        opacity: 0;
    }

    100% {
        opacity: 100%;
    }
}

.active-pink-4 input[type=text]:focus:not([readonly]) {
    border: 1px solid rgb(253, 53, 67);
    box-shadow: 0 0 0 1px rgb(253, 53, 67);
}

.active-pink-3 input[type=text] {
    border: 1px solid rgb(253, 53, 67);
    box-shadow: 0 0 0 1px rgb(253, 53, 67);
}
